@import url(https://fonts.googleapis.com/css?family=Lato:300,400,700);
@import url(https://fonts.googleapis.com/css?family=Anton);
.Badge {
  background: #FFFFFF;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.10);
  border-radius: 8px 8px 8px 8px;
  overflow: hidden;
}

.Badge__header {
  padding: 0.5rem 0;
  height: 80px;
  background: #1B1B25;
  display: flex;
  justify-content: center;
}

.Badge__section-name {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 1rem 0;
}

.Badge__section-name h1{
  font-size: 2.5rem;
}

.Badge__section-info {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 0.5rem 0;
  background: #F4F4F7;
}


.Badge__avatar {
  border-radius: 50%;
  margin-right: 1rem;
  width: 120px;
  height: 120px;
}

.Badge__footer {
  height: 54px;
  display: flex;
  justify-content: center;
  align-items: center;
  color: #98CA3F;
  font-weight: bold;
  font-style: italic;
}

@media screen and (max-width: 600px) {
  .Badge__section-name h1{
    font-size: 2.5rem;
  }

  .Badge__avatar {
    
    width: 100px;
    height: 100px;
  }
}

@media screen and (max-width: 400px) {
  .Badge__section-name h1{
    font-size: 2rem;
  }

  .Badge__avatar {
    
    width: 100px;
    height: 100px;
  }
}
.BadgeNew__hero {
  width: 100%;
  padding: 2rem 0;
  background: url(/gasparnd/react-course/static/media/stars.1d17360a.svg), #1B1B25;
  background-repeat: repeat;
  margin-bottom: 1rem;
  color: #ffffff;
  display: flex;
  justify-content: center;
} 

.BadgeNew__badge-container {
	margin-bottom: 2rem;
}

.BadgeNew__badge-info {
	display: grid;
	grid-template-columns: repeat(2, auto);
	grid-gap: 2rem;
}

@media screen and (max-width: 780px) {
	.BadgeNew__badge-info {
		grid-template-columns: 1fr;
		grid-template-rows: repeat(2, auto);
	}

	.BadgeNew__badge {
		grid-row: 0/1;
		grid-column: 0/1;
	}
}

@media screen and (max-width: 780px) {
	.BadgeNew__badge-info {
		grid-template-columns: 1fr;
		grid-template-rows: repeat(2, auto);

	}

	.BadgeNew__badge {
		grid-row: 0/1;
		grid-column: 0/1;
	}

	.BadgeNew__hero img {
		width: 150px;
	}
}
.PageLoading {
	height: 100vh;
	display: flex;
	justify-content: center;
	align-items: center;
	font-size: 1.5rem;
	font-weight: bold;
}
.BadgesList ul>li {
  margin-bottom: 1rem;
}

.BadgesListItem {
  background: #FFFFFF;
  box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.23);
  border-radius: 5px;
  display: flex;
  padding: 1rem;
}

.BadgesListItem__avatar {
  border-radius: 50%;
  background: #a3a3a3;
  margin-right: 0.5rem;
  width: 80px;
  height: 80px;
}

.Badges__list {
  display: grid;
  justify-content: center;
}

.Badges__list-item {
  margin: 0;
  border-radius: 5px;
  display: grid;
  grid-template-columns: 1fr 2fr;
  align-items: center;
  padding: .5rem;
  background: #fff;
}

.Badges__item-img img {
  border-radius: 50%;
}

.Badges__list-info h3, a {
  margin: 0;
}

.Badges__list-info p {
  margin: 0;
  font-weight: bold;
}

.Badges__list-info h3 {
  font-size: 1.5rem;
}

@media screen and (max-width: 450px) {
  .BadgesListItem {
    padding: .5rem;
  }

  .Badges__list-item {
    padding: 0;
    width: 100%;
    
  }
}
.MiniLoading {
	width: 100%;
	display: flex;
	justify-content: center;
	font-size: 1.3rem;
	font-weight: bold;
}
.PageError {
	height: 100vh;
	display: grid;
	grid-template-rows: 2fr 1fr;
	justify-items: center;
	font-size: 1.5rem;
	font-weight: bold;
	margin-top: 2rem;
}



@media media and screen(max-width: 800px) {
	.giphy-embed {
		width: 250px;
		height: 250px;
	}
}
.Badges__hero {
  width: 100%;
  padding: 2rem 0;
  background: url(/gasparnd/react-course/static/media/stars.1d17360a.svg), #1B1B25;
  background-repeat: repeat;
  margin-bottom: 1rem;
}

.Badges__hero>.Badges__container {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.Badges__container {
  width: 100%;
  max-width: 580px;
  margin: 0 auto;
  padding: 0 1rem;
}

.Badges_conf-logo {
  margin-bottom: 2rem;
}

.Badges__buttons {
  display: flex;
  justify-content: flex-end;
  margin-bottom: 1rem;
  margin-right: 2rem;
}

@media screen and (max-width: 780px) {
  .Badges_conf-logo {
    width: 250px;
  }
}

@media screen and (max-width: 450px) {
  .Badges_conf-logo {
    width: 200px;
  }

  .Badges__buttons {
    display: flex;
    justify-content: center;
    margin-bottom: 1rem;
    margin-right: 0;
  }
}
