.Modal {
  position: fixed;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  background-color: rgba(0, 0, 0, 0.5);

  display: flex;
  flex-direction: column;
  align-items: center;
}

.Modal__container {
  position: relative;
  top: 10rem;
  background-color: #ffffff;
  padding: 1rem;
  width: 400px;
}

.Modal__close-button {
  position: absolute;
  top: 0;
  right: 0;
  border: 0;
  background-color: #F5F5F5;
  padding: 0.5rem 1rem;
}
.BadgeDetails__hero {
  width: 100%;
  padding: 2rem 0;
  background: url(/gasparnd/react-course/static/media/stars.1d17360a.svg), #1B1B25;
  background-repeat: repeat;
  margin-bottom: 1rem;
  color: #ffffff;
  display: grid;
  grid-template-columns: 1fr 1fr;
  justify-items: center;
  align-items: center;
}

.BadgeDetails__info {
  display: grid;
  grid-template-columns: 2fr 1fr;  
  grid-gap: 1rem;
}

.BadgeDetails__info-actions {
  align-self: center;
}


@media screen and (max-width: 780px) {
  .BadgeDetails__hero {
    grid-template-columns: 1fr; 
    grid-template-rows: 1fr 1fr;
  }

  .BadgeDetails__hero-logo img {
    width: 280px;
  }

  .BadgeDetails__info {
    grid-template-columns: auto;
    grid-template-rows: 1fr auto; 
  }

  .BadgeDetails__info-buttons {
    display: flex;
    justify-content: space-around;
    margin-bottom: 1rem;
  }

}

@media screen and (max-width: 500px) {
  .BadgeDetails__hero-logo img {
    width: 230px;
  }

  .BadgeDetails__hero-attendant-name h1 {
    font-size: 2rem;
  }
}
.Home__hero {
	height: 80vh;
	width: 100%;
	display: flex;
	flex-direction: column;
	justify-content: center;
	grid-gap: 2rem;
	gap: 2rem;
	padding: 4rem;
	background: url(/gasparnd/react-course/static/media/stars.1d17360a.svg), #1B1B25;
  	background-repeat: repeat;
}

.Home__hero img {
	width: 300px;
}

.Home__hero a {
	width: -webkit-max-content;
	width: -moz-max-content;
	width: max-content;
}

@media screen and (max-width: 720px) {
	.Home__hero {
		padding: 3rem;
	}

	.Home__hero img {
		width: 250px;
	}
}
.Navbar {
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 0.5rem 0;
  background-color: #1C3643;
}

.Navbar__brand {
  color: #ffffff;

  display: flex;
  align-items: center;
}

.Navbar__brand:hover {
  color: #ffffff;
  text-decoration: none;
}

.Navbar__brand-logo {
  margin-right: 0.5rem;
}
.Footer {
	width: 100%;
	display: flex;
	justify-content: center;
	align-items: center;
	background-color: #1C3643;
	padding: .5rem;
}

.Footer a {
	text-decoration: none;
	color: #ffff;
}

.Footer a:hover {
	text-decoration: underline;
	color: #ffff;
}
* {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

body {
  font-family: 'Lato', 'Helvetica Neue', sans-serif;
  background-color: #F4F4F7;
}

h1 {
  font-family: 'Anton', sans-serif;
}

a.link-unstyled {
  color: inherit;
}

a.link-unstyled:hover {
  color: inherit;
  text-decoration: none;
}

.btn {
  padding: 0.375rem 2rem;
}

.btn.btn-primary {
  color: #fff;
  background-color: #7DCD40;
  border-color: #7DCD40;
}

.btn.btn-primary:not(:disabled):not(.disabled):hover {
  color: #fff;
  background-color: #7DCD40;
  border-color: #7DCD40;
}

.btn.btn-primary:not(:disabled):not(.disabled):active {
  color: #fff;
  background-color: #7DCD40;
  border-color: #7DCD40;
}

.btn.btn-primary:not(:disabled):not(.disabled):focus {
  box-shadow: 0 0 0 0.2rem rgba(125, 205, 64, 0.5);
}

.btn.btn-danger {
  color: #fff;
  background-color: #CD4040;
  border-color: #CD4040;
}

.btn.btn-danger:not(:disabled):not(.disabled):hover {
  color: #fff;
  background-color: #CD4040;
  border-color: #CD4040;
}

.btn.btn-danger:not(:disabled):not(.disabled):active {
  color: #fff;
  background-color: #CD4040;
  border-color: #CD4040;
}

.btn.btn-danger:not(:disabled):not(.disabled):focus {
  box-shadow: 0 0 0 0.2rem rgba(255, 64, 64, 0.5);
}
