.Badges__hero {
  width: 100%;
  padding: 2rem 0;
  background: url('../../images/stars.svg'), #1B1B25;
  background-repeat: repeat;
  margin-bottom: 1rem;
}

.Badges__hero>.Badges__container {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.Badges__container {
  width: 100%;
  max-width: 580px;
  margin: 0 auto;
  padding: 0 1rem;
}

.Badges_conf-logo {
  margin-bottom: 2rem;
}

.Badges__buttons {
  display: flex;
  justify-content: flex-end;
  margin-bottom: 1rem;
  margin-right: 2rem;
}

@media screen and (max-width: 780px) {
  .Badges_conf-logo {
    width: 250px;
  }
}

@media screen and (max-width: 450px) {
  .Badges_conf-logo {
    width: 200px;
  }

  .Badges__buttons {
    display: flex;
    justify-content: center;
    margin-bottom: 1rem;
    margin-right: 0;
  }
}